import React from 'react';
import { render, screen, fireEvent } from '@testing-library/react';
import RoleManagementPage from '../RoleManagementPage';

describe('RoleManagementPage', () => {
  it('renders the Role Management heading', () => {
    render(<RoleManagementPage />);
    expect(screen.getByText('Role Management')).toBeInTheDocument();
  });

  it('opens the create role modal when Create Role is clicked', () => {
    render(<RoleManagementPage />);
    fireEvent.click(screen.getByText('Create Role'));
    expect(screen.getByText(/Create Role|Edit Role/)).toBeInTheDocument();
  });

  it('closes the modal when Cancel is clicked', () => {
    render(<RoleManagementPage />);
    fireEvent.click(screen.getByText('Create Role'));
    fireEvent.click(screen.getByText('Cancel'));
    expect(screen.queryByText(/Create Role|Edit Role/)).not.toBeInTheDocument();
  });
});
